/* Projects.module.css */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.header {
  font-size: 1.8em;
  color: #f8f8f2;
  margin-bottom: 20px;
}

.card {
  background: #21252b;
  border-radius: 2px;
  display: inline-block;
  height: auto;
  margin: 1rem;
  position: relative;
  width: 300px;
  padding: 20px;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  color: #f8f8f2;
}

/* Add this new class */
.wider-card {
  width: 400px;
}

.card h3 {
  font-size: 1.3em;
  color: #f8f8f2;
  margin-bottom: 10px;
}

.card h4 {
  font-size: 1.1em;
  color: #a9a9a9;
  margin-bottom: 10px;
}

.card ul {
  padding-left: 20px;
  margin-bottom: 10px;
}

.card a {
  color: #50fa7b;
  text-decoration: none;
}

/* Mobile styles */
@media (max-width: 768px) {
  .card {
    width: 95%;
  }

  .wider-card {
    width: 100%;
  }
}

